@font-face {
  font-family: "Staatliches";
  src: url("Staatliches-Regular.eot");
  src: url("Staatliches-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Staatliches-Regular.woff2") format("woff2"), url("../fonts/Staatliches-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  src: url("Barlow-Regular.eot");
  src: url("Barlow-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay";
  src: url("SFProDisplay-Regular.eot");
  src: url("SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("ProximaNova-Regular.eot");
  src: url("ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.search__filters {
  margin: 80px 0;
}

.navigation__items-filters {
  font-family: "SFProDisplay";
  font-size: 20px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  color: #000;
}

.navigation__item-filterActiv {
  background-color: #F0F0F4;
  padding: 11px 26px 12px 26px;
  font-weight: 700;
  border-radius: 5px;
}

.form {
  position: relative;
}

.form input {
  width: 1157px;
  padding-left: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: "SFProDisplay";
  font-size: 16px;
  font-weight: 400;
  text-transform: none;
  text-align: left;
  color: #656464;
  background-color: #F0F0F4;
  border: none;
  border-radius: 3px;
}

.form button {
  position: absolute;
  right: -5px;
  top: 0;
  font-family: "SFProDisplay";
  font-size: 14px;
  font-weight: 700;
  text-transform: center;
  text-align: left;
  color: #FFF;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  background: #1C62CD;
  padding: 17px 38px 16px 40px;
  border-radius: 3px;
}

@media (max-width: 800px) {
  .navigation__items-filters {
    white-space: nowrap;
    overflow: height;
  }
  .form {
    margin: 0;
  }
  .form input {
    width: 700px;
    padding-left: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .form button {
    display: none;
  }
}