@font-face {
  font-family: "Staatliches";
  src: url("Staatliches-Regular.eot");
  src: url("Staatliches-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Staatliches-Regular.woff2") format("woff2"), url("../fonts/Staatliches-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  src: url("Barlow-Regular.eot");
  src: url("Barlow-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay";
  src: url("SFProDisplay-Regular.eot");
  src: url("SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("ProximaNova-Regular.eot");
  src: url("ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.stocks__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-top: 81px;
}

.slider {
  width: 870px;
  overflow: hidden;
  position: relative;
}

.slider-products {
  width: 1150px;
  margin-top: 35px;
  padding-right: 50px;
  padding-bottom: 5px;
  padding-top: 10px;
}

.slider__row {
  position: relative;
  display: flex;
}

.slider__trigers {
  position: absolute;
  display: flex;
  bottom: 40px;
  left: 50%;
  transform: translate(-50%);
  z-index: 2;
}

.trigers__item--active {
  margin-left: 0;
  background-color: #fff;
}

.trigers_item {
  margin-left: 9px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  border: 1px solid #fff;
}
.trigers_item:hover {
  cursor: pointer;
}

.slider__arrow {
  position: absolute;
  z-index: 3;
  top: 50%;
  transform: translateY(-50%);
}
.slider__arrow img {
  width: 100%;
}
.slider__arrow:hover {
  cursor: pointer;
}

.slider__arrow--left {
  left: 23px;
}

.slider__arrow--right {
  right: 14.87px;
}

.slider__blackArrow--left {
  left: 0;
}

.slider__blackArrow--right {
  right: 0;
}

.sale__wrapper {
  border-radius: 3px;
  border: 1px solid #CDCDCD;
}

.sale__header {
  display: flex;
  justify-content: space-between;
  align-items: start;
  padding-right: 4px;
}

.saleHeader_text--stocks {
  font-family: "ProximaNova";
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  padding: 8px 19.31px 8.8px 20px;
  background-color: #1C62CD;
  line-height: 15px;
  letter-spacing: 0.1em;
}

.saleHeader__price {
  font-family: "Barlow";
  font-size: 27px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #1C62CD;
}

.sale__img {
  text-align: center;
  margin-top: 36px;
}

.saleHeader__oldPrice {
  text-align: right;
  position: relative;
  margin-top: 7px;
  display: flex;
  justify-content: flex-end;
  line-height: 17px;
  font-family: "Barlow";
  font-size: 14px;
  font-weight: 400;
  text-transform: lowercase;
  text-align: none;
  color: #C4C4C4;
}

.oldPrice__text {
  margin-top: 3px;
}

.oldPrice__img {
  margin-left: 3.93px;
  padding-right: 4px;
  margin-top: 2px;
}

.saleHeader__oldPrice::before {
  content: " ";
  position: absolute;
  content: " ";
  width: 60px;
  height: 0.5px;
  background-color: #C4C4C4;
  transform: rotate(-5.33deg);
  top: 8px;
  right: 4px;
}

.sale__title {
  font-family: "SFProDisplay";
  font-size: 18px;
  font-weight: 700;
  text-transform: none;
  text-align: center;
  color: #000;
  margin-top: 4.31px;
}

.sale__top {
  margin-top: 7px;
  background-color: #F0F0F4;
  padding: 41px 36px 48px 40px;
}

.sale__description {
  font-family: "SFProDisplay";
  font-size: 21px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  color: #000;
}

.sale__date {
  font-family: "Barlow";
  font-size: 18px;
  font-weight: 700;
  text-transform: none;
  text-align: center;
  color: #1C62CD;
  line-height: 22px;
}

.slider__trigers-products {
  display: flex;
}

@media (max-width: 1192px) {
  .slider {
    width: 890;
    margin: 0 20px;
  }
  .slider__row-mobile img {
    width: 800px;
  }
  .slider-products {
    width: 1000px;
    margin-top: 35px;
    padding-right: 50px;
    padding-bottom: 5px;
  }
}
@media (max-width: 992px) {
  .slider {
    width: 500px;
    margin: 0 20px;
  }
  .slider__row-mobile img {
    width: 500px;
  }
  .slider-products {
    width: 260px;
    margin: 35px auto;
    padding-right: 50px;
    padding-bottom: 30px;
  }
  .slider__trigers-products {
    display: flex;
    bottom: 0;
  }
  .trigers__item--activeProducts {
    background-color: #1C62CD;
  }
  .trigers_item-products {
    background-color: #C4C4C4;
  }
  .slider__arrow {
    display: none;
  }
  .stocks__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 81px;
    flex-direction: column;
  }
  .sale__wrapper {
    margin-top: 40px;
    width: 500px;
  }
}
@media (max-width: 576px) {
  .slider {
    width: 300px;
    margin: 0 20px;
  }
  .slider__row-mobile img {
    width: 300px;
  }
  .sale__wrapper {
    width: 265px;
  }
  .slider__trigers {
    bottom: 20px;
  }
  .trigers_item {
    width: 7.04px;
    height: 7px;
  }
  .slider__trigers-products {
    bottom: 0px;
  }
  .trigers_item-products {
    height: 14px;
    width: 13.68px;
  }
  .trigers__item--activeProducts {
    height: 14px;
    width: 13.68px;
  }
  .slider-products {
    width: 260px;
    margin: 10px auto;
  }
}