@font-face {
  font-family: "Staatliches";
  src: url("Staatliches-Regular.eot");
  src: url("Staatliches-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Staatliches-Regular.woff2") format("woff2"), url("../fonts/Staatliches-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  src: url("Barlow-Regular.eot");
  src: url("Barlow-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay";
  src: url("SFProDisplay-Regular.eot");
  src: url("SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("ProximaNova-Regular.eot");
  src: url("ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.main__banner {
  margin-top: 86px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #1C62CD;
  padding: 28px 123px 14px 71px;
}

.bunner__description {
  font-family: "SFProDisplay";
  font-size: 38px;
  font-weight: 700;
  text-transform: none;
  text-align: left;
  color: #fff;
  line-height: 36px;
  margin-left: 40px;
}

.bunner__button .main__button {
  background-color: #fff;
  font-family: "SFProDisplay";
  font-size: 24px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: right;
  color: #2F3035;
  line-height: 50px;
  padding: 0 41px;
  margin-left: 50px;
}

.main__banner {
  display: flex;
}

.main__banner_mobile {
  display: none;
}

@media (max-width: 1192px) {
  .main__banner {
    display: none;
  }
  .main__banner_mobile {
    flex-direction: column;
    display: flex;
  }
  .bunner__description {
    font-family: "SFProDisplay";
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    line-height: 18px;
  }
  .bunner__button .main__button {
    background-color: #fff;
    font-family: "SFProDisplay";
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;
    text-align: right;
    color: #2F3035;
    line-height: 50px;
    padding: 0 41px;
    margin-left: 0;
    white-space: nowrap;
  }
  .bunner__botom {
    display: flex;
    align-items: center;
  }
  .bunner__image img {
    width: 70px;
    height: 70px;
  }
  .bunner__image_quadro {
    margin-left: 30px;
  }
}