@font-face {
  font-family: "Staatliches";
  src: url("Staatliches-Regular.eot");
  src: url("Staatliches-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Staatliches-Regular.woff2") format("woff2"), url("../fonts/Staatliches-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  src: url("Barlow-Regular.eot");
  src: url("Barlow-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay";
  src: url("SFProDisplay-Regular.eot");
  src: url("SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("ProximaNova-Regular.eot");
  src: url("ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.footer {
  background-color: #F0F0F4;
}

.footer__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 98px;
  padding-top: 42px;
  padding-bottom: 27px;
}

.footer__subscription {
  margin-top: 14px;
}

.form .form__input-subscription {
  margin-top: 34px;
  width: 201px;
  padding-top: 8px;
  padding-bottom: 10px;
  padding-left: 12px;
  font-family: "SFProDisplay";
  font-size: 14px;
  font-weight: 400;
  text-transform: none;
  text-align: left;
  color: #2F3035;
  background-color: #fff;
}

.form__icon {
  display: none;
}

.subscription__text {
  font-family: "SFProDisplay";
  font-size: 14px;
  font-weight: 700;
  text-transform: none;
  text-align: center;
  color: #2F3035;
  line-height: 17px;
}

.form .form__button-subscription {
  font-family: "SFProDisplay";
  font-size: 14px;
  font-weight: 400;
  text-transform: none;
  text-align: left;
  color: white;
  right: -70px;
  top: 33px;
  padding: 10px 16px;
}
.form .form__button-subscription .footer__menu {
  margin-top: 20px;
}

.menu__title {
  font-family: "SFProDisplay";
  font-size: 14px;
  font-weight: 700;
  text-transform: none;
  text-align: left;
  color: #2F3035;
}

.footer__contacts {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.footer__menu {
  margin-top: 20px;
}

.footer__icon {
  margin-left: 50px;
}
.footer__icon :hover {
  cursor: pointer;
}

.footer__conditionality {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 46px;
}

.conditionality__text {
  font-family: "SFProDisplay";
  font-size: 14px;
  font-weight: 400;
  text-transform: lowercase;
  text-align: left;
  color: #48494D;
  padding-bottom: 27px;
  text-align: center;
}

.conditionality__text:nth-child(1) {
  margin-right: 100px;
}

.conditionality__text:nth-child(2) {
  margin-right: 20px;
}

@media (max-width: 576px) {
  .footer {
    background-color: #fff;
  }
  .footer__info {
    align-items: baseline;
    margin-top: 83px;
  }
  .footer__conditionality {
    white-space: nowrap;
    flex-direction: column;
    align-items: baseline;
  }
}
@media (max-width: 1192px) {
  .footer__info {
    flex-direction: column;
    align-items: baseline;
  }
  .menu__items-footer {
    display: none;
  }
  .footer__icon {
    margin-left: 30px;
  }
  .footer__icon:nth-child(1) {
    margin-left: 50px;
  }
  .footer__contacts {
    margin-top: 61px;
  }
  .menu__title {
    color: #7F7F7F;
    font-weight: 500;
  }
  .form .form__button-subscription {
    display: block;
  }
  .form__icon {
    display: block;
    position: absolute;
    top: 13px;
    right: 15px;
  }
}