@font-face {
  font-family: "Staatliches";
  src: url("Staatliches-Regular.eot");
  src: url("Staatliches-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Staatliches-Regular.woff2") format("woff2"), url("../fonts/Staatliches-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  src: url("Barlow-Regular.eot");
  src: url("Barlow-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay";
  src: url("SFProDisplay-Regular.eot");
  src: url("SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("ProximaNova-Regular.eot");
  src: url("ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.header__top {
  font-family: "SFProDisplay";
  font-size: 20px;
  font-weight: 700;
  text-transform: lowercase;
  text-align: center;
  color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 25px;
}

.navigation__items {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navigation__item {
  margin-left: 50px;
  list-style: none;
}
.navigation__item:hover {
  cursor: pointer;
}

.navigation__item--margin {
  margin-left: 0;
}

.header__logo {
  flex-direction: column;
}

.logo_image {
  display: block;
}

.logo__text {
  font-family: "Staatliches";
  font-size: 20px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  color: #000;
  margin-top: 8.84px;
  line-height: 25px;
}

.header__lacation {
  font-family: "SFProDisplay";
  font-size: 20px;
  font-weight: 700;
  text-transform: lowercase;
  text-align: center;
  color: #000;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.location {
  position: relative;
}

.location__pointer {
  position: absolute;
  bottom: 0;
  left: -27px;
}

.header__user {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header__userdiv {
  margin-left: 10px;
}

.user__favorites:hover {
  cursor: pointer;
}

.user__profile:hover {
  cursor: pointer;
}

.user__cart:hover {
  cursor: pointer;
}

.header__menu {
  margin-top: 21px;
}

.menu {
  background-color: #F0F0F4;
}

.menu-products {
  background-color: #fff;
}

.menu__items {
  padding: 15px 0 14px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__items-footer {
  flex-direction: column;
  line-height: 17px;
  align-items: flex-start;
  padding: 0;
}
.menu__items-footer li {
  margin-top: 5px;
}
.menu__items-footer li a {
  color: #2F3035;
}

.menu__link {
  font-family: "SFProDisplay";
  font-size: 20px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  color: #2F3035;
  line-height: 23.87px;
}

.menu__link--active {
  font-weight: 600;
  position: relative;
  padding-bottom: 14px;
  border-bottom: 3px solid #1C62CD;
}

.header__icon {
  display: none;
}

.menu__items-products {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navigation {
  display: block;
}

.navigation_mobile {
  display: none;
}

#burger {
  display: none;
}

@media (max-width: 1192px) {
  .header__top {
    text-align: left;
  }
  .logo_image {
    display: none;
  }
  .header__lacation {
    display: none;
  }
  .navigation {
    display: none;
  }
  .navigation_mobile {
    display: none;
  }
  #burgerClose + label + .navigation_mobile {
    display: none;
  }
  #burger:checked + label + .navigation_mobile {
    display: flex;
  }
  .menu__link {
    margin-left: 15px;
  }
  .header__icon {
    display: block;
  }
  .header__icon:hover {
    cursor: pointer;
  }
  .navigation__item {
    margin: 10px 0;
    position: relative;
  }
  .navigation__item::before {
    position: absolute;
    content: " ";
    width: 600px;
    bottom: 0;
    border: 1px solid #EEE;
    left: -100px;
  }
  .navigation__item a {
    margin-left: 23px;
    color: #2F3035;
  }
  .navigation__items_mobile {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 5;
    background-color: #fff;
    top: 0;
    left: 0;
    flex-direction: column;
  }
  .navigation__item_margin {
    margin: 10px 20px;
  }
}
@media (max-width: 756px) {
  .menu__items-footer {
    display: none;
  }
}