@import './fonts'
@import './common'    
@import './variables'

     
.header__top 
    @include font( 'SFProDisplay' ,20px, 700, lowercase, center, $header_top_color )
    @include displayElements( flex, space-between , center,)
    margin-top: 25px


.navigation__items
     @include displayElements( flex, flex-start , center,)


.navigation__item
    margin-left: 50px 
    list-style: none
    &:hover
        cursor: pointer

.navigation__item--margin
    margin-left: 0
    
.header__logo 
    flex-direction: column
.logo_image
    display: block    
   
.logo__text
      @include font( 'Staatliches',20px, 400,  uppercase , center, $header_top_color )   
      margin-top: 8.84px
      line-height: 25px


.header__lacation
      @include font( 'SFProDisplay' ,20px,  700, lowercase, center, $header_top_color )
      @include displayElements( flex, flex-start , center,)
      
  
.location 
    position: relative

.location__pointer
    position: absolute  
    bottom: 0
    left: -27px
    
.header__user 
    @include displayElements( flex, flex-start , center,)

    &div
        margin-left: 10px
    
    
.user__favorites
    &:hover
        cursor: pointer 

.user__profile 
     &:hover
        cursor: pointer 

.user__cart 
     &:hover
        cursor: pointer 

.header__menu
    margin-top: 21px 
  

.menu
      background-color: #F0F0F4
      
.menu-products
    background-color:#fff 

.menu__items
    padding: 15px 0 14px 0
    @include displayElements( flex, space-between , center,)
    
.menu__items-footer
    flex-direction: column
    line-height: 17px
    align-items: flex-start
    padding: 0
    
    li
        margin-top: 5px

    li a
        color: #2F3035

.menu__link
      @include font( 'SFProDisplay' ,20px, 400, none, center, $main_text )
      line-height: 23.87px
     
   
.menu__link--active
    font-weight: 600
    position: relative
    padding-bottom: 14px
    border-bottom: 3px solid #1C62CD

.header__icon
    display: none

.menu__items-products
    @include displayElements( flex, flex-start , center,)
.navigation
    display: block    
.navigation_mobile
     display: none  

#burger
        display: none
@media (max-width:1192px)
    .header__top
        text-align: left
    .logo_image
        display: none
    .header__lacation
        display: none
    .navigation
        display: none    
    .navigation_mobile
        display: none
    #burgerClose + label + .navigation_mobile
        display: none
    #burger:checked + label + .navigation_mobile
        display: flex


    .menu__link
        margin-left: 15px
   
    .header__icon
        display: block
        &:hover
            cursor: pointer

    .navigation__item
        margin: 10px 0
        position: relative
        
    .navigation__item
         &::before
            position: absolute
            content: ' '
            width: 600px
            bottom: 0
            border: 1px solid #EEE
            left: -100px
           
    .navigation__item a
       margin-left: 23px
       color: #2F3035
    .navigation__items_mobile
        display: flex
        justify-content: flex-start
        align-items: baseline 
        position: fixed
        width: 100%
        height: 100%
        z-index: 5
        background-color: #fff
        top: 0
        left: 0

        flex-direction: column

    .navigation__item_margin
        margin:  10px 20px
    

@media (max-width: 756px) 
     .menu__items-footer
        display: none

    
  
    
        
        



