* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.main__categories {
  margin-top: 80px;
}

.categories__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: none;
}

.categories__wrapper div:nth-child(1) {
  margin-left: 0;
}

.categories__item {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-star;
  font-family: "Barlow";
  font-size: 28px;
  font-weight: 700;
  text-transform: none;
  text-align: left;
  color: #2F3035;
  border: 1px solid #CDCDCD;
  border-radius: 3px;
  padding: 20px 37.79px 14px 20px;
  margin-left: 30px;
  flex-basis: 370px;
}
.categories__item:hover {
  cursor: pointer;
}

.categories__item-active {
  color: #1C62CD;
  box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.25);
}

.categories__wrapper-bottom {
  margin-top: 30px;
}

.categories__title {
  line-height: 28px;
}

.categories__link {
  position: absolute;
  bottom: 14px;
  left: 20px;
}
.categories__link a {
  font-family: "Barlow";
  font-size: 14px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  color: #C4C4C4;
}
.categories__link a:hover {
  cursor: pointer;
}

.categories__item-padding {
  padding-right: 24px;
}

@media (max-width: 1192px) {
  .categories__wrapper {
    flex-wrap: wrap;
  }
}
@media (max-width: 990px) {
  .categories__wrapper {
    align-items: center;
    justify-content: center;
  }
  .categories__item {
    margin-left: 0;
    margin-top: 30px;
  }
}