@charset "UTF-8";
@font-face {
  font-family: "Staatliches";
  src: url("Staatliches-Regular.eot");
  src: url("Staatliches-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Staatliches-Regular.woff2") format("woff2"), url("../fonts/Staatliches-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Barlow";
  src: url("Barlow-Regular.eot");
  src: url("Barlow-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Barlow-Regular.woff2") format("woff2"), url("../fonts/Barlow-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SFProDisplay";
  src: url("SFProDisplay-Regular.eot");
  src: url("SFProDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("ProximaNova-Regular.eot");
  src: url("ProximaNova-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1171px;
  margin: 0 auto;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  border: none;
}

button:hover {
  cursor: pointer;
}

@media (max-width: 992px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (max-width: 1192px) {
  .container {
    max-width: 1140px;
    margin: 0 auto;
  }
}
@media (max-width: 576px) {
  .container {
    max-width: 500px;
    margin: 0 10px;
    overflow: hidden;
  }
}
@media (max-width: 450px) {
  .container {
    max-width: 450px;
  }
}
.main__popular {
  margin-top: 80px;
}

.title__text {
  font-family: "Barlow";
  font-size: 25px;
  font-weight: 700;
  text-transform: none;
  text-align: left;
  color: #2F3035;
  line-height: 30px;
}

.product__cart {
  position: absolute;
  bottom: 0px;
  right: 0;
  padding: 10px 16px 7.5px 14px;
  background-color: #1C62CD;
  border-radius: 10px 0px 0px 0px;
}

.product {
  border-radius: 3px;
  text-align: center;
  position: relative;
  padding: 16px 19px 31px 40px;
  margin-left: 30px;
  font-family: "SFProDisplay";
  font-size: 18px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  color: #2F3035;
  box-shadow: 3px 3px 20px rgba(50, 50, 50, 0.25);
  border-radius: 3px;
}
.product div {
  margin-top: 14px;
}

.product__title {
  line-height: 21.48px;
}

.product__prise {
  font-family: "Barlow";
  font-size: 30px;
  font-weight: 700;
  text-transform: none;
  text-align: center;
  color: none;
}

.product :hover:after {
  position: absolute;
  content: "посмотреть товар";
  cursor: pointer;
  top: 40%;
  left: 40%;
  transform: translate(-40%, -40%);
  width: 230px;
  height: 51px;
  background-color: #FFFFFF;
  opacity: 0.9;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  padding-top: 18px;
  font-family: "SFProDisplay";
  font-size: 20px;
  font-weight: 400;
  text-transform: none;
  text-align: center;
  color: #2F3035;
}

.product__cart:hover:after {
  content: none;
}

.product__cart:hover {
  cursor: pointer;
}

.product__sale {
  position: absolute;
  top: -15px;
  left: 0;
  width: 63px;
  height: 25px;
  padding-top: 6px;
  background-color: #1C62CD;
  font-family: "SFProDisplay";
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  line-height: 14.32px;
}

.product__sale:hover:after {
  content: none;
}

.product__favorit {
  position: absolute;
  right: 19px;
  top: 16px;
}

.product__favorit:hover:after {
  content: none;
}

.product__favorit:hover {
  cursor: pointer;
}

.product__prise-noPrise {
  font-family: "SFProDisplay";
  font-size: 18px;
  font-weight: 700;
  text-transform: none;
  text-align: center;
  color: none;
}

.product:hover {
  color: #1C62CD;
  cursor: pointer;
}

.mein__menu-products {
  margin-top: 25px;
}

.menu__items-products li {
  margin-left: 50px;
}

.menu__items-products li:nth-child(1) {
  margin-left: 0px;
}

.main__more {
  margin-top: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main__button {
  font-family: "SFProDisplay";
  font-size: 15px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  color: #2F3035;
  padding: 17px 43px 15px 43px;
  background-color: #F0F0F4;
  letter-spacing: 0.12em;
  line-height: 18px;
  text-align: center;
  border: none;
}

.main__button:hover {
  cursor: pointer;
}

.main__frequent {
  margin-top: 72px;
}

.main__more-mobile {
  display: none;
}

@media (max-width: 992px) {
  .main__more-mobile {
    display: flex;
  }
}